.Logo {
  height: 2.5rem;
  padding: 0 1rem;
  border: 1px solid;
  display: inline-flex;
  align-items: center;
  text-transform: uppercase;
  letter-spacing: 0.25em;
  text-align: center;
  text-decoration: none;
  color: inherit;
}
.Nav {
  display: flex;
}

  .Nav-Link {
    position: relative;
    display: inline-block;
    display: flex;
    align-items: center;
    text-align: center;
    vertical-align: middle;
    padding: 1.25em 1.5em;
    border-bottom: 2px solid transparent;
    text-transform: uppercase;
    text-decoration: none;
    color: inherit;
    font-weight: 500;
    letter-spacing: 0.02em;
  }

    .Nav-Link:hover {
      background-color: hsl(205, 100%, 96%);
    }

    .Nav-Link_active {
      border-bottom-color: currentColor;
    }
.Dashboard {
  padding: 1.5rem;
}

  .Dashboard-Title {
    margin: 0;
  }
.Select {
  margin: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  display: inline-block;
  max-width: 100%;
  box-sizing: border-box;
  height: 2.25em;
  padding: 0 1em;
  padding-right: 1.5em;
  border: 1px solid hsl(205, 10%, 78%);
  font: inherit;
  border-radius: 0;
  background-color: #f8f8f8;
  background-image:
    url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="hsl(205, 10%, 32%)" d="M7,10L12,15L17,10H7Z" /></svg>');
  background-repeat: no-repeat;
  background-position: right 0 top 50%;
  background-size: 24px 24px;
}

  .Select::-ms-expand {
    display: none;
  }

  .Select option {
    font-weight:normal;
  }

  .Select:hover {
    border-color: hsl(205, 7%, 66%);
  }

  .Select:focus {
    outline: 0;
    border-color: hsl(205, 79%, 48%);
  }
.Report {
  flex: 1 1;
  display: flex;
  flex-direction: column;
}

  .Report-Head {
    flex: 0 0 auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
  }

  .Report-Title,
  .Report-Tools {
    padding: 0.25rem;
  }

  .Report-Title {
    margin: 0;
  }

  .Report-Tools {
    display: grid;
    grid-column-gap: 0.5rem;
    grid-auto-flow: column;
  }

  .Report-Body {
    flex: 1 1;
    display: flex;
    overflow: auto;
    padding: 0 1.5rem 1rem;
  }

    .Report-Body > * {
      flex: 1 1;
      min-width: 0;
      min-height: 0;
    }
:root {
  font: 87.5%/1 BlinkMacSystemFont, -apple-system, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  /* color: hsl(205, 100%, 28%); */
  color: hsl(205, 10%, 32%);
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

.App {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
}

  .App-Head,
  .App-Foot {
    flex: 0 0 auto;
    display: flex;
    justify-content: space-between;
    padding: 0 1.5rem;
    border: 0px solid hsl(205, 10%, 78%);
  }

  .App-Head {
    border-bottom-width: 1px;
  }

  .App-Logo {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }

  .App-Foot {
    padding-top: 1rem;
    padding-bottom: 1rem;
    border-top-width: 1px;
  }

  .App-Body {
    flex: 1 1;
    display: flex;
  }

